import { IQuote } from '../Interfaces/Quotes';
import { mocaQuotes } from '../MoocasData/Quotes';

interface Param {
  id?: string;
  cnpj?: string;
  days?: number;
  status?: string;
}
export const QuoteService = {
  Get: ({ status = '0', days = 0, id, cnpj }: Param) => {
    const date = new Date();
    if (days) date.setDate(date.getDate() - days);

    return new Promise<IQuote[]>(resolve => {
      const quotes: IQuote[] = [];
      setTimeout(() => {
        const result = mocaQuotes
          .filter(
            q =>
              (q.id === id || q.cnpj === cnpj) &&
              q.dtEmission.toDate() <= date &&
              q.idStatus === status
          )
          ?.sort(
            (a, b) =>
              b.dtEmission.toDate().getTime() - a.dtEmission.toDate().getTime()
          );

        if (result.length) quotes.push(result[0]);
        resolve(quotes);
      }, 2000);
    });
  }
};
