import React, { useCallback, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import {
  CenterPanel,
  Column,
  LabelCustom,
  LeftPanel,
  Panel,
  RightPanel,
  Row,
  RowPanel,
  Text
} from '../../components';
import { useMainContext } from '../../Context/Main';
import { IActiveFilters, IQuote } from '../../Interfaces/Quotes';
import { QuoteService } from '../../services/quoteService';
import {
  ArrowIcon,
  ButtonFilter,
  Container,
  Filters,
  HandIcon,
  Title,
  WrapperFilters,
  WrapperPanel
} from './styles';

const filterIntial = {
  time: ['Aberto', 'Vencido', 'Finalizado', 'Todos'],
  status: ['Última', '30 dias', '60 dias', '> 90 dias']
};

const Quotes: React.FC = () => {
  const [quotes, setQuotes] = useState<IQuote[]>();
  const [hasResult, setHasResult] = useState<boolean>(false);

  const [activeFilters, setActiveFilters] = useState<IActiveFilters>({
    statusIndex: 0,
    timeIndex: 0
  });

  const { setLoading } = useMainContext();

  const handlerBtnFilter = useCallback((filterName: string, index: number) => {
    setActiveFilters(prevState => ({ ...prevState, [filterName]: index }));
  }, []);

  useEffect(() => {
    const getQuotes = async () => {
      try {
        setLoading(true);
        const result = await QuoteService.Get({ cnpj: '11.000.000/0000-00' });
        setLoading(false);
        setQuotes(result);
        setHasResult(!result.length);
        console.log('---setQuotes', 'result');
      } catch (err) {
        console.log(err);
      }
    };
    getQuotes();
  }, [setLoading]);

  return (
    <Container>
      <Title>Cotações</Title>

      <WrapperFilters>
        <Filters>
          {filterIntial.time.map((text, index) => (
            <ButtonFilter
              key={text}
              className={index === activeFilters.timeIndex ? 'active' : ''}
              onClick={() => {
                handlerBtnFilter('timeIndex', index);
              }}
            >
              {text}
            </ButtonFilter>
          ))}
        </Filters>
        <Filters>
          {filterIntial.status.map((text, index) => (
            <ButtonFilter
              key={text}
              className={index === activeFilters.statusIndex ? 'active' : ''}
              onClick={() => {
                handlerBtnFilter('statusIndex', index);
              }}
            >
              {text}
            </ButtonFilter>
          ))}
        </Filters>
      </WrapperFilters>

      <WrapperPanel>
        {hasResult && (
          <>
            <Panel backgroundColor="panel-warning">
              Não foi possível encontrar as Cotação
            </Panel>
            <br />
          </>
        )}
        {quotes &&
          quotes?.map(quote => (
            <Link key={quote.id} to={`/quotesDetail/${quote.id}`}>
              <RowPanel>
                <LeftPanel>
                  <HandIcon />
                </LeftPanel>
                <CenterPanel>
                  <Row>
                    <Column>
                      <LabelCustom>cód</LabelCustom>
                      <Text>{quote.id}</Text>
                    </Column>
                    <Column>
                      <LabelCustom>status</LabelCustom>
                      <Text className={`color${quote.idStatus}`}>
                        {quote.status}
                      </Text>
                    </Column>
                  </Row>
                  <Row>
                    <Column>
                      <LabelCustom>Dt. Emissão</LabelCustom>
                      <Text>{quote.dtEmission}</Text>
                    </Column>
                    <Column>
                      <LabelCustom>Dt. Limite</LabelCustom>
                      <Text>{quote.dtLimit}</Text>
                    </Column>
                  </Row>
                </CenterPanel>
                <RightPanel>
                  <ArrowIcon />
                </RightPanel>
              </RowPanel>
            </Link>
          ))}
      </WrapperPanel>
    </Container>
  );
};

export default Quotes;
