import React, { useCallback, useState } from 'react';
import {
  CenterPanel,
  Column,
  LabelCustom,
  LeftPanel,
  RightPanel,
  Row,
  RowPanel,
  Text
} from '..';
import { useContextQuoteItems } from '../../Context/QuoteItems';
import { Panel } from '../Panel';
import QuotesItemsForm from '../QuotesItemsForm';
import { ArrowDownIcon, ArrowUpIcon, Container, TagIcon } from './styles';

const QuotesItems: React.FC = () => {
  const [quoteItemId, setQuoteItemId] = useState<string>('');

  const { quoteItemsContext } = useContextQuoteItems();

  const handlerCollapse = useCallback((id: string) => {
    setQuoteItemId(prevState => (prevState === id ? '' : id));

    setTimeout(() => {
      const location = window.document.getElementById(id)?.offsetTop;
      if (location)
        window.document
          .getElementById('main')
          ?.scrollTo({ top: location, behavior: 'auto' });
    }, 10);
  }, []);

  return (
    <Container>
      {!quoteItemsContext && (
        <>
          <br />
          <Panel backgroundColor="panel-warning">
            Não foi possível encontrar os itens para essa Cotação
          </Panel>
        </>
      )}
      {quoteItemsContext?.map(({ id, itemId, product, total }) => (
        <RowPanel
          key={id}
          id={id}
          onClick={() => handlerCollapse(id)}
          className={
            quoteItemId === '' || quoteItemId === id
              ? 'rowPanel'
              : ' hide rowPanel'
          }
        >
          <LeftPanel>
            <TagIcon />
          </LeftPanel>
          <CenterPanel>
            <Row>
              <Column>
                <LabelCustom>Item</LabelCustom>
                <Text>{itemId}</Text>
              </Column>
            </Row>
            <Row>
              <Column>
                <LabelCustom>Produto</LabelCustom>
                <Text colorFont="blue-light">{product}</Text>
              </Column>

              <Column width="50%">
                {total && (
                  <>
                    <LabelCustom>Total</LabelCustom>
                    <Text>R$ {total}</Text>
                  </>
                )}
              </Column>
            </Row>
          </CenterPanel>
          <RightPanel>
            <ArrowUpIcon className={quoteItemId === id ? 'active' : ''} />
            <ArrowDownIcon className={quoteItemId === id ? '' : 'active'} />
          </RightPanel>
        </RowPanel>
      ))}
      {quoteItemId && (
        <QuotesItemsForm
          quoteItemId={quoteItemId}
          handlerCollapse={handlerCollapse}
        />
      )}
    </Container>
  );
};

export default React.memo(QuotesItems);
