import styled from 'styled-components';
import { Warning } from '../../styles/Icons';

export const Container = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;

  &.invalid {
    span {
      color: var(--error-alert);
      font-size: 10px;
      display: flex;
      align-items: center;
    }
    input {
      border-color: var(--error-alert);
    }
    svg {
      display: block;
    }
  }

  .react-switch-checkbox {
    height: 0;
    width: 0;
    visibility: hidden;
  }

  .react-switch-label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    width: 40px;
    height: 22px;
    background: grey;
    border-radius: 100px;
    position: relative;
    transition: background-color 0.2s;
  }

  .react-switch-label {
    .react-switch-button {
      content: '';
      position: absolute;
      top: 1px;
      left: 2px;
      width: 20px;
      height: 20px;
      border-radius: 45px;
      transition: 0.2s;
      background: #fff;
      box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
    }
  }

  .react-switch-checkbox:checked {
    + .react-switch-label .react-switch-button {
      left: calc(100% - 2px);
      transform: translateX(-100%);
    }
    + .react-switch-label {
      background: var(--switch-active);
    }
  }

  .react-switch-label:active .react-switch-button {
    width: 30px;
  }
`;

export const IconWarning = styled(Warning)`
  display: none;
  width: 10px;
  height: 10px;
  flex-shrink: 0;
  margin-right: 5px;
  path {
    color: var(--error-alert);
  }
`;

export const Input = styled.input`
  font-size: 14px;
  border: 1px solid var(--status-default);
  border-radius: 4px;
  padding: 5px 10px;
  width: ${({ type }) => (type === 'checkbox' ? 'auto' : '100%')};
  box-sizing: border-box;
  ::placeholder {
    color: var(--placeholder-color);
    text-transform: uppercase;
    font-size: 10px;
  }
  ::-webkit-inner-spin-button {
    display: none;
  }
  &:disabled {
    background-color: inherit;
    color: inherit;
    border-color: inherit;
    cursor: initial;
    opacity: 0.4;
  }
`;
