import React from 'react';
import { useMainContext } from '../../Context/Main';
import { Container, SpinnerIcon } from './styles';

const Loading: React.FC = () => {
  const { loading } = useMainContext();
  return (
    <Container show={loading}>
      <SpinnerIcon />
      Processando...
    </Container>
  );
};

export default Loading;
