import React, { useCallback, useEffect, useState } from 'react';
import { SubmitHandler, useForm } from 'react-hook-form';
import { Button, Column, InputForm, LabelCustom, Panel, Row } from '..';
import { useMainContext } from '../../Context/Main';
import { useContextQuoteItems } from '../../Context/QuoteItems';
import { IQuoteItem, IQuotesItemForm } from '../../Interfaces/QuotesDetail';
import { IPropsQuotesItemsForm } from '../../Interfaces/QuotesItemsForm';
import { QuoteItemsService } from '../../services/quoteItemsService';
import { Text } from '../Text';
import { Container, Form, WrapperInputs } from './styles';

const QuotesItemsForm: React.FC<IPropsQuotesItemsForm> = ({
  quoteItemId,
  handlerCollapse
}) => {
  const { setLoading } = useMainContext();
  const [quoteItem, setQuoteItem] = useState<IQuoteItem>();
  const [required, setRequired] = useState<boolean>(true);
  const { quoteItemsContext, setQuoteItemsContext } = useContextQuoteItems();
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors }
  } = useForm<IQuotesItemForm>();

  const onSubmit: SubmitHandler<IQuotesItemForm> = useCallback(
    async (item: IQuotesItemForm) => {
      try {
        if (quoteItem) {
          const total = item.unitValue.toFloat() * quoteItem.amount;

          const newQuoteItem = {
            ...quoteItem,
            ...item,
            total: total.toFloatBr()
          };

          // salvar no endpoint
          setLoading(true);
          const response = await QuoteItemsService.Update(newQuoteItem);
          setLoading(false);

          const newQuoteItems = quoteItemsContext?.map(q =>
            q.id === newQuoteItem.id ? newQuoteItem : q
          );

          setQuoteItemsContext(newQuoteItems);

          handlerCollapse(quoteItem.id);
        }
      } catch (err) {
        console.log('error in save QuotesItemsForm', err);
      }
    },
    [
      handlerCollapse,
      quoteItem,
      quoteItemsContext,
      setLoading,
      setQuoteItemsContext
    ]
  );
  const handlerHasProduct = useCallback(
    (e: React.MouseEvent<HTMLInputElement>) => {
      const { checked } = e.currentTarget;
      setRequired(checked);
      if (!checked) {
        reset({
          referenceCode: '',
          unitValue: '0',
          ipiValue: '0',
          icmsValue: '0',
          deliveryTime: 0,
          observation: '',
          total: ''
        } as IQuoteItem);
      }
    },
    [reset]
  );
  useEffect(() => {
    if (quoteItemId) {
      const item = quoteItemsContext?.find(quote => quote.id === quoteItemId);
      reset(item);
      setQuoteItem(item);
    }
  }, [quoteItemId, quoteItemsContext, reset]);

  return (
    <Container className={quoteItem?.id ? '' : 'hide'}>
      <Form onSubmit={handleSubmit(onSubmit)} noValidate>
        <WrapperInputs>
          <Panel>
            <Row>
              <Column>
                <LabelCustom>Cód. Produto</LabelCustom>
                <Text>{quoteItem?.productCode}</Text>
              </Column>
              <Column>
                <LabelCustom>Quantidade</LabelCustom>
                <Text>
                  {quoteItem?.amount} <small>{quoteItem?.unit}</small>
                </Text>
              </Column>
              <Column>
                <InputForm
                  label="Possui Produto?"
                  register={register('hasItem')}
                  type="checkbox"
                  errors={errors}
                  onClick={handlerHasProduct}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Cod. Referência"
                  register={register('referenceCode', {
                    required: { value: required, message: 'campo obrigatório' },
                    maxLength: {
                      value: 10,
                      message: 'Valor máximo de 10 caracteres'
                    }
                  })}
                  placeholder="Digite o Código"
                  type="text"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Valor Unitário"
                  register={register('unitValue', {
                    required: { value: required, message: 'campo obrigatório' }
                  })}
                  type="text"
                  placeholder="Digite o Valor"
                  mask="currency"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Aliq. IPI(%)"
                  register={register('ipiValue', {
                    required: { value: required, message: 'campo obrigatório' }
                  })}
                  type="text"
                  placeholder="Digite o IPI"
                  mask="currency"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Aliq. ICMS(%)"
                  register={register('icmsValue', {
                    required: { value: required, message: 'campo obrigatório' }
                  })}
                  type="text"
                  mask="currency"
                  placeholder="Digite o ICMS"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Prazo Entrega(Dias)"
                  register={register('deliveryTime', {
                    required: { value: required, message: 'campo obrigatório' }
                  })}
                  placeholder="Digite o(s) dia(s)"
                  type="number"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  disabled={!required}
                  label="Observação (Opcional)"
                  register={register('observation', {
                    maxLength: {
                      value: 50,
                      message: 'Valor máximo de 50 caracteres'
                    }
                  })}
                  placeholder="Digite uma Observação"
                  type="text"
                  errors={errors}
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <Button type="submit" outlined>
                  Salvar
                </Button>
              </Column>
            </Row>
          </Panel>
        </WrapperInputs>
      </Form>
    </Container>
  );
};

export default React.memo(QuotesItemsForm);
