import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import {
  Button,
  Column,
  InputForm,
  LabelCustom,
  Panel,
  Row,
  SelectForm,
  Text
} from '../../components';
import QuotesItems from '../../components/QuotesItems';
import { useMainContext } from '../../Context/Main';
import { useContextQuoteItems } from '../../Context/QuoteItems';
import {
  IParamQuoteId,
  IPaymentConditionsSelect,
  IQuoteCSV,
  IQuoteItem,
  IQuoteItemHeader,
  IQuoteItemHeaderForm,
  IShippingTypeSelect
} from '../../Interfaces/QuotesDetail';
import {
  mocaQuoteItemHeader,
  mocaSelectPaymentConditions,
  mocaSelectShipping
} from '../../MoocasData/QuotesDetail';
import { QuoteItemsService } from '../../services/quoteItemsService';
import { buildURI } from '../../utils/exportCSV';
import {
  Container,
  IconCsv,
  IconHand,
  IconPrint,
  IconSave,
  Import,
  Title,
  WrapperExport,
  WrapperIcons,
  WrapperImport,
  WrapperImportExport,
  WrapperPrint,
  WrapperSave,
  WrapperTitleQuotes,
  WrapperTop
} from './styles';

const QuotesDetail: React.FC = () => {
  const { quoteId } = useParams<IParamQuoteId>();

  const { quoteItemsContext, setQuoteItemsContext } = useContextQuoteItems();

  const [selectShipping, setSelectShipping] = useState<IShippingTypeSelect[]>(
    []
  );

  const [selectPaymentConditions, setSelectPaymentConditions] = useState<
    IPaymentConditionsSelect[]
  >([]);

  const [quoteItemHeader, setQuoteItemHeader] = useState<IQuoteItemHeader>();

  const [href, setHref] = useState('');

  const linkExportCSV = useRef<HTMLAnchorElement>(null);

  const MapQuoteItemToQuoteCSV = useCallback(
    (data: IQuoteItem[]) =>
      data.map(
        q =>
          ({
            id: q.id,
            CodigoProduto: q.productCode,
            Disponivel: q.hasItem ? 'sim' : 'nao',
            CodigoDeReferencia: q.referenceCode,
            Produto: q.product,
            ValorUnitario: q.unitValue,
            ValorIPI: q.ipiValue,
            ValorICMS: q.icmsValue,
            PrazoEntrega: q.deliveryTime,
            Observacao: q.observation
          } as IQuoteCSV)
      ),
    []
  );

  const MapQuoteCSVToQuoteItem = useCallback(
    (data: IQuoteCSV[]) =>
      data.map(
        q =>
          ({
            id: q.id,
            productCode: q.CodigoProduto,
            hasItem: q.Disponivel === 'sim',
            referenceCode: q.CodigoDeReferencia,
            product: q.Produto,
            unitValue: q.ValorUnitario,
            ipiValue: q.ValorIPI,
            icmsValue: q.ValorICMS,
            deliveryTime: q.PrazoEntrega,
            observation: q.Observacao
          } as IQuoteItem)
      ),
    []
  );

  const exportCSV = useCallback(() => {
    const newData = MapQuoteItemToQuoteCSV(quoteItemsContext);
    const URI = buildURI(newData);

    linkExportCSV?.current?.setAttribute('download', 'download.csv');
    linkExportCSV?.current?.setAttribute('hidden', '');
    linkExportCSV?.current?.setAttribute('href', URI);

    linkExportCSV?.current?.click();
  }, [MapQuoteItemToQuoteCSV, quoteItemsContext]);

  const importCSV = useCallback(() => {
    const csv = '';
  }, []);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors }
  } = useForm<IQuoteItemHeaderForm>();

  const { setLoading } = useMainContext();

  useEffect(() => {
    // pegar valor de endpoint
    setQuoteItemHeader(mocaQuoteItemHeader);
    reset(mocaQuoteItemHeader);
    // pegar valor de endpoint
    setSelectShipping(mocaSelectShipping);
    // pegar valor de endpoint
    setSelectPaymentConditions(mocaSelectPaymentConditions);

    const getQuoteItems = async () => {
      try {
        setLoading(true);
        const items = await QuoteItemsService.Get(quoteId);
        setLoading(false);
        setQuoteItemsContext(items);
      } catch (err) {
        console.log(err);
      }
    };

    getQuoteItems();
  }, [quoteId, reset, setQuoteItemsContext, setLoading]);

  return (
    <Container>
      <WrapperTop>
        <WrapperImportExport>
          <WrapperExport>
            <span>Exportar</span>
            <WrapperIcons>
              <IconCsv onClick={() => exportCSV()} />
              <a
                target="_blank"
                ref={linkExportCSV}
                href="empty"
                style={{ display: 'none' }}
                rel="noreferrer"
              >
                link
              </a>
            </WrapperIcons>
          </WrapperExport>
          <WrapperImport>
            <span>Importar</span>
            <Import>
              <input id="file-upload" type="file" required />
              <label htmlFor="file-upload">
                <IconCsv />
              </label>
              <WrapperSave className="import">
                <IconSave onClick={() => importCSV} />
                <span>Salvar</span>
              </WrapperSave>
            </Import>
          </WrapperImport>
        </WrapperImportExport>
        <WrapperTitleQuotes>
          <IconHand />
          <Title>cotação</Title>
          <span>{quoteId}</span>
        </WrapperTitleQuotes>
        <WrapperPrint>
          <span>Imprimir</span>
          <WrapperIcons>
            <IconPrint />
          </WrapperIcons>
        </WrapperPrint>
      </WrapperTop>
      <Panel>
        <Row>
          <Column>
            <LabelCustom>Empresa</LabelCustom>
            <Text>{quoteItemHeader?.buyer}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Data Emissão</LabelCustom>
            <Text>{quoteItemHeader?.issuanceDate}</Text>
          </Column>
          <Column>
            <LabelCustom>Data Limite</LabelCustom>
            <Text>{quoteItemHeader?.deadline}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>CEP</LabelCustom>
            <Text>{quoteItemHeader?.zipCode}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Endereço</LabelCustom>
            <Text>{quoteItemHeader?.address}</Text>
          </Column>
          <Column>
            <LabelCustom>Numero</LabelCustom>
            <Text>{quoteItemHeader?.number}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>Complemento</LabelCustom>
            <Text>{quoteItemHeader?.complement}</Text>
          </Column>
          <Column>
            <LabelCustom>Bairro</LabelCustom>
            <Text>{quoteItemHeader?.neighborhood}</Text>
          </Column>
        </Row>
        <Row>
          <Column>
            <LabelCustom>UF</LabelCustom>
            <Text>{quoteItemHeader?.state}</Text>
          </Column>
          <Column>
            <LabelCustom>Cidade</LabelCustom>
            <Text>{quoteItemHeader?.city}</Text>
          </Column>
        </Row>
        <form style={{ width: '100%' }}>
          <Row>
            <Column>
              <SelectForm
                register={register('shippingTypeId', {
                  required: 'Campo Obrigatório'
                })}
                label="Tipo Frete"
              >
                <option key="-1" value="-1">
                  Selecione
                </option>
                {selectShipping.map(
                  ({ shippingTypeId: id, shippingType: type }) => (
                    <option value={id} key={id}>
                      {type}
                    </option>
                  )
                )}
              </SelectForm>
            </Column>
          </Row>
          <Row>
            <Column>
              <SelectForm
                register={register('paymentConditionsId', {
                  required: 'Campo Obrigatório'
                })}
                label="Cond. Pagamento"
              >
                <option value="-1" key="-1">
                  Selecione
                </option>
                {selectPaymentConditions.map(
                  ({
                    paymentConditionsId: id,
                    paymentCondition: condition
                  }) => (
                    <option value={id} key={id}>
                      {condition}
                    </option>
                  )
                )}
              </SelectForm>
            </Column>
          </Row>
          <Row>
            <Column>
              <InputForm
                label="Valor Frete"
                register={register('shippingValue', {
                  required: 'Campo Obrigatório'
                })}
                placeholder="Digite o Valor"
                type="number"
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <Button type="submit" outlined>
                Salvar
              </Button>
            </Column>
          </Row>
        </form>
      </Panel>

      <QuotesItems />
    </Container>
  );
};
export default QuotesDetail;
