const toCSV = (obj: any, h?: string[]) => {
  const csvRows = [];

  const headers: string[] = h ?? Object.keys(obj[0]);
  csvRows.push(headers.join(','));

  obj.forEach((o: any) => {
    const values = headers.map((header: string) => {
      const escaped = `${o[header]}`.replace(/"/g, '\\"');
      return `"${escaped}"`;
    });
    csvRows.push(values.join(','));
  });
  return csvRows.join('\n');
};

export const buildURI = (obj: any, header?: string[]) => {
  const csv = toCSV(obj, header);
  console.log('csv---', csv);
  const textEncoder = new TextEncoder();
  const csvEncoded = textEncoder.encode(csv);
  const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
  const type = isSafari
    ? 'application/csv;charset=utf-8'
    : 'text/csv;charset=utf-8';
  const blob = new Blob([new Uint8Array([0xef, 0xbb, 0xbf]), csvEncoded], {
    type
  });
  const dataURI = `data:${type};${csvEncoded}`;

  const URL = window.URL || window.webkitURL;

  return typeof URL.createObjectURL === 'undefined'
    ? dataURI
    : URL.createObjectURL(blob);
};
