import { IQuoteItem } from '../Interfaces/QuotesDetail';
import { mocaQuotesItems } from '../MoocasData/QuotesDetail';

export const QuoteItemsService = {
  Update: (quoteItem: IQuoteItem) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    }),

  Get: (quoteId: String) =>
    new Promise<IQuoteItem[]>(resolve =>
      setTimeout(() => {
        const result = mocaQuotesItems.filter(q => q.quoteId === quoteId);
        resolve(result?.length ? result : []);
      }, 2000)
    )
};
