import styled, { css } from 'styled-components';
import { KeyboardArrowDown, KeyboardArrowUp, Tag } from '../../styles/Icons';

export const Container = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 100vw;
  margin-top: 15px;
  .rowPanel {
    cursor: pointer;
    * {
      cursor: pointer;
    }
  }
  .rowPanel.hide {
    display: none;
  }
`;

export const TagIcon = styled(Tag)`
  width: 24px;
  height: 24px;
`;

const arrowIconCSS = css`
  width: 32px;
  height: 32px;
  display: none;
  &.active {
    display: block;
  }
`;

export const ArrowUpIcon = styled(KeyboardArrowUp)`
  ${arrowIconCSS}
`;

export const ArrowDownIcon = styled(KeyboardArrowDown)`
  ${arrowIconCSS}
`;
