/* eslint-disable func-names */
/* eslint-disable no-extend-native */

declare global {
  interface String {
    toDate(): Date;
    toFloat(): number;
  }
  interface Number {
    toFloatBr(): string;
  }
}

String.prototype.toDate = function (this: String) {
  const date = this.split('/').map(d => parseInt(d, 10));

  return new Date(date[2], date[1] - 1, date[0]);
};

String.prototype.toFloat = function (this: String) {
  return parseFloat(this.replaceAll('.', '').replaceAll(',', '.'));
};

Number.prototype.toFloatBr = function (this: Number) {
  return this.toFixed(2).toString().replaceAll('.', ',');
};

// eslint-disable-next-line prettier/prettier
export { };
