import styled from 'styled-components';
import { Warning } from '../../styles/Icons';

export const Container = styled.label`
  display: flex;
  width: 100%;
  flex-direction: column;

  &.invalid {
    span {
      color: var(--error-alert);
      font-size: 10px;
      display: flex;
      align-items: center;
    }
    select {
      border-color: var(--error-alert);
    }
    svg {
      display: block;
    }
  }
`;

export const Select = styled.select`
  font-size: 14px;
  border: 1px solid var(--status-default);
  border-radius: 4px;
  padding: 5px;
  width: 100%;
  &:hover {
    transition: all 0.3s;
    font-size: 14.3px;
  }
`;

export const IconWarning = styled(Warning)`
  display: none;
  width: 10px;
  height: 10px;
  flex-shrink: 0;
  margin-right: 5px;
  path {
    color: var(--error-alert);
  }
`;
